
import { defineComponent } from "vue";
export default defineComponent({
  name: "BarraLateral",
  data() {
    return {
      modoEscuro: false,
    };
  },
  computed: {
    textButton() {
      if (this.modoEscuro) {
        return "Desativar modo escuro";
      }
      return "Ativar Modo escuro";
    },
  },
  emits: ["aoTemaAlterado"],
  methods: {
    alterarTema() {
      this.modoEscuro = !this.modoEscuro;
      this.$emit("aoTemaAlterado");
    },
  },
});
